import React, { useState, useEffect } from 'react';
import PhoneInput from 'react-phone-input-2';
import 'react-phone-input-2/lib/style.css';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { motion, AnimatePresence } from 'framer-motion';

function PopUpContainer({ onCloseButton, onClose }) {
  const [customTime, setCustomTime] = useState('');
  const [name, setName] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [selectedPackage, setSelectedPackage] = useState('');
  const [timeSelection, setTimeSelection] = useState(' ');
  const [isImmediateTimeSelected, setIsImmediateTimeSelected] = useState(true);
  const [isOpenByButton, setIsOpenByButton] = useState(false);

  useEffect(() => {
    setCustomTime('');
  }, [isOpenByButton]);

  if (isOpenByButton) {
    onCloseButton();
  }

  const showErrorNotification = () => {
    toast.error('!    .', {
      position: 'top-right',
      autoClose: 5000,
    });
  };

  const showSuccessNotification = () => {
    toast.success('  ', {
      position: 'top-right',
      autoClose: 5000,
    });
  };

  const handleSubmit = (event) => {
    event.preventDefault();

    if (name && phoneNumber && selectedPackage && (timeSelection !== '' || customTime)) {
      const formData = {
        name,
        phoneNumber,
        selectedTime: timeSelection === ' ' ? timeSelection : customTime,
        selectedPackage,
      };

      console.log('  :', formData);

      showSuccessNotification();

      resetForm();
      onClose();
      setIsOpenByButton(true);
    } else {
      showErrorNotification();
    }
  };

  const resetForm = () => {
    setName('');
    setPhoneNumber('');
    setSelectedPackage('');
    setCustomTime('');
  };

  const popupVariants = {
    hidden: { opacity: 0, y: '100%' },
    visible: { opacity: 1, y: 0 },
  };

  return (
    <AnimatePresence>
      <motion.div
        className={`popUp-container ${isImmediateTimeSelected ? 'immediate-time' : ''}`}
        variants={popupVariants}
        initial="hidden"
        animate="visible"
        exit="hidden">
        {onCloseButton && (
          <svg
            onClick={onCloseButton}
            className="popUp-container--closeButton"
            width="2.5rem"
            height="2.5rem"
            viewBox="0 0 24 24"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
            stroke="#BF1616">
            <g id="SVGRepo_bgCarrier" strokeWidth="0"></g>
            <g
              id="SVGRepo_tracerCarrier"
              strokeLinecap="round"
              strokeLinejoin="round"
              stroke="#Bf1616"
              strokeWidth="0.288"></g>
            <g id="SVGRepo_iconCarrier">
              <path
                d="M14.5 9.50002L9.5 14.5M9.49998 9.5L14.5 14.5"
                stroke="#BF1616"
                strokeWidth="1.5"
                strokeLinecap="round"></path>
            </g>{' '}
          </svg>
        )}
        <form onSubmit={handleSubmit}>
          <label>
             '
            <input
              type="text"
              name=" '"
              placeholder="'"
              value={name}
              onChange={(e) => setName(e.target.value)}
              style={{ borderRadius: '11px' }}
              autoComplete="off"
              required
            />
          </label>
          <label>
             
            <PhoneInput
              name="number"
              value={phoneNumber}
              onChange={setPhoneNumber}
              country={'ua'}
              enableSearch
              inputStyle={{
                width: '100%',
                border: '1px solid black',
                borderRadius: '11px',
                height: '3rem',
              }}
              placeholder="  "
              autoComplete="off"
              required
            />
          </label>
          <label>
              ?
            <select
              value={timeSelection}
              onChange={(e) => {
                setTimeSelection(e.target.value);
                setIsImmediateTimeSelected(e.target.value === ' ');
                if (e.target.value === '') {
                  setCustomTime('');
                }
              }}
              name="  ?"
              required>
              <option value=" "> </option>
              <option value=""></option>
            </select>
            {timeSelection === '' && (
              <input
                type="datetime-local"
                value={customTime}
                onChange={(e) => setCustomTime(e.target.value)}
                style={{ marginTop: '10px', borderRadius: '11px', width: '100%' }}
                autoComplete="off"
                required
              />
            )}
          </label>
          <p className="popUp-container--text">    ?</p>
          <label className="radio-label">
            <input
              type="radio"
              name=" "
              value="    "
              onChange={(e) => setSelectedPackage(e.target.value)}
              style={{}}
              required
            />
                
          </label>
          <label className="radio-label">
            <input
              type="radio"
              name=" "
              value="-"
              onChange={(e) => setSelectedPackage(e.target.value)}
              style={{}}
              required
            />
            -
          </label>
          <button className="popUp-container--button" onClick={handleSubmit}>
            
          </button>
        </form>
      </motion.div>
    </AnimatePresence>
  );
}

export default PopUpContainer;
